.body {
  margin: 0;
}

.card {
  border: 1px solid #f007e4;
  flex-direction: column;
  justify-content: space-between;
  width: 199px;
  height: 100%;
  display: flex;
}

.card__image {
  box-sizing: border-box;
  background-image: url("imac.202c52ff.jpeg");
  background-size: cover;
  width: 160px;
  height: 134px;
  margin-top: 33px;
  margin-left: 20px;
}

.card__name {
  text-align: left;
  width: 166px;
  height: 36px;
  padding: 40px 16px 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  text-align: left;
  color: #616070;
  width: 98px;
  height: 14px;
  padding: 4px 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card:hover {
  border: 1px solid #00f;
  border-radius: 5px;
}

.card__price {
  flex-direction: row;
  justify-content: space-between;
  padding: 24px 16px 0;
  display: flex;
}

.card__price-key {
  text-align: left;
  color: #616070;
  width: 31px;
  height: 18px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  text-align: right;
  color: #060b35;
  width: 50px;
  height: 18px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.review {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 16px 16px 0;
  display: flex;
}

.review__reviews {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  flex-direction: row;
  height: 16px;
  display: flex;
}

.stars__star-active {
  box-sizing: border-box;
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  align-items: center;
  width: 16px;
  height: 16px;
  margin: 0 4px 0 0;
}

.stars__star {
  box-sizing: border-box;
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  align-items: center;
  width: 16px;
  height: 16px;
  margin: 0 4px 0 0;
}

.stars__star-active:last-child, .stars__star:last-child {
  margin-right: 0;
}

.button {
  color: #fff;
  background: #00acdc;
  border: 0;
  border-radius: 5px;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}
